/* -----------------------------------------------------------------------------
  Component: Version selector

  Style for the release version selector.
----------------------------------------------------------------------------- */
header .tooltip {
  font-family: Manrope, sans-serif;
}

#version-selector.show .dropdown-toggle::after {
  transform: rotate(180deg);
}

#version-selector .dropdown-btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  transition: color 0s;
  justify-content: space-between;
  width: calc((385px + 32px) / 2);
  height: 40px;
  border: 2px solid;
  border-radius: 10px;
}

@media (max-width: 575px) {
  #version-selector .dropdown-btn {
    font-size: 14px;
  }
}

@media (max-width: 1199px) {
  #version-selector .dropdown-btn {
    border: none;
    padding: 0;
    width: auto;
  }
}

#version-selector .dropdown-btn:focus {
  box-shadow: none;
}

@media (max-width: 1199px) {
  #version-selector .dropdown-btn span {
    padding-right: 5px;
  }
}

#version-selector .dropdown-menu {
  top: 52px;
  box-sizing: border-box;
  width: 100%;
  border: 2px solid;
  border-radius: 10px;
}

@media (max-width: 575px) {
  #version-selector .dropdown-menu {
    font-size: 14px;
  }
}

@media (max-width: 1199px) {
  #version-selector .dropdown-menu {
    border: none;
    padding: 0;
    max-height: calc(100vh - 70px - 56px);
    overflow-y: auto;
    left: -10px !important;
  }
}

#version-selector .dropdown-menu > li > a {
  display: inline-block;
  width: 100%;
  padding: .16rem .75rem;
  text-decoration: none;
}

#version-selector .dropdown-menu > li > a:hover, #version-selector .dropdown-menu > li > a:focus {
  font-weight: 500;
}

#version-selector .dropdown-menu > li > a.disabled {
  font-weight: 500;
}

#version-selector .dropdown-menu > li > a.disabled:hover, #version-selector .dropdown-menu > li > a.disabled:focus {
  font-weight: 500;
}

