}

header .navbar-toggler {
  padding: .4rem;
}

header .navbar-toggler .navbar-toggler-icon,
header .navbar-toggler .navbar-toggler-icon::before,
header .navbar-toggler .navbar-toggler-icon::after {
  width: 24px;
  height: 3px;
  border-radius: 6px;
  border: 1px solid;
}

header .navbar-toggler .navbar-toggler-icon {
  position: relative;
}

header .navbar-toggler .navbar-toggler-icon::before, header .navbar-toggler .navbar-toggler-icon::after {
  position: absolute;
  content: "";
  display: block;
  left: -1px;
}

header .navbar-toggler[aria-expanded="false"] .navbar-toggler-icon {
  transition: background-color .5s .3s, border-color .5s .3s;
}

header .navbar-toggler[aria-expanded="false"] .navbar-toggler-icon::before {
  top: -8px;
  transform: rotateZ(0deg);
  transition: all .5s;
}

header .navbar-toggler[aria-expanded="false"] .navbar-toggler-icon::after {
  bottom: -8px;
  transform: rotateZ(0deg);
  transition: all .5s;
}

header .navbar-toggler[aria-expanded="true"] .navbar-toggler-icon {
  background-color: transparent;
  border-color: transparent;
}

header .navbar-toggler[aria-expanded="true"] .navbar-toggler-icon::before {
  top: -1px;
  transform: rotateZ(45deg);
  transition: all .5s;
}

header .navbar-toggler[aria-expanded="true"] .navbar-toggler-icon::after {
  bottom: -1px;
  transform: rotateZ(-45deg);
  transition: all .5s;
}

header .search-toggler {
  margin-right: 3px;
}

header .search-toggler .search-toggler-icon {
  display: block;
  width: 32px;
  height: 32px;
}

