/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  border: 2px solid;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background-repeat: no-repeat;
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* Light theme -------------------------------------------------------------- */
body .search-box {
  background-color: #fff;
  border-color: #c1ccd9;
}

body .search-box .form-control {
  background-color: #fff;
  color: #292929;
}

body .search-box .btn-search {
  background: url("../../images/icons/svg-icons.svg#search-lt");
}

body .search-box ::placeholder {
