}

/* -----------------------------------------------------------------------------
  Component: Global TOC

  Style for the menu containing the full index of the documentation release
  folder.
  Note: the elements of the global TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#global-toc {
  /* Hide toctree-l7 and its branch */
}

@media (max-width: 1199px) {
  #global-toc {
    pointer-events: all;
    width: 100%;
    margin-bottom: 0;
    padding-bottom: 10px;
    max-height: calc(100vh - 70px);
    overflow-y: auto;
  }
}

@media (min-width: 1200px) {
  #global-toc.collapse:not(.show) {
    display: block;
  }
}

#global-toc ul {
  padding: 0;
  list-style: none;
}

#global-toc ul a {
  padding-right: 60px;
}

@media (min-width: 1200px) and (max-width: 1599px) {
  #global-toc ul a {
    padding-right: 48px;
  }
}

#global-toc ul .current-toc-node:hover .toc-toggle-btn {
  display: block;
}

#global-toc ul .current-toc-node::before {
  position: absolute;
  top: 1.1rem;
  display: inline-block;
